<template lang="">
    <div class="_full_router financial-detail-content">
       <!-- <custHeader :title="title"></custHeader> -->
       <div class="login-header">
         <div class="top-back" @click="goBack">
           <span class="mint-button-icon"><i class="mintui mintui-back"></i></span>
         </div>
   	  <div style="float:right" class="cart-link">
   	  <router-link :to="{path:'search'}"> <span class="mint-button-icon"><i class="mintui mintui-search"></i></span></router-link>
   	  </div>

       </div>
       <div class="pic-contain">
       		<mt-swipe :auto="0">
					<mt-swipe-item >
						<img :src="img1" >
					</mt-swipe-item>
					<mt-swipe-item >
						<img :src="img1" >
					</mt-swipe-item>

				</mt-swipe>
       </div>
       <div class="text-contain">
       		 <div class="goods-base-info">
		       	<div class="goods-name">18k金相和田白玉方形吊坠</div>
		       	<div class="price-sales">￥12222 <span>优惠促销</span></div>
		    	<div class="price-o">价格<span>￥15555</span></div>
		    	<div class="normal-box  "><div>快递:0</div><div>已售：1111</div><div>杭州</div></div>
    		</div>
    		<div class="big-title">详情</div>
    		<div class="no-pad">
    			<img src="../../assets/2276.png" >
    			<img src="../../assets/2276.png" >
    			<img src="../../assets/2276.png" >
    		</div>
       </div>
      	<footer class="fix-buy">
      	<button type="button" class="add-cart" @click="addCartShow">加入购物车</button><button type="button" class="add-buy">立即购买</button>
      	</footer>
        <mt-popup v-model="pVisible" position="bottom" class="add-cart-con">
                <div class="add-cart-contain">
                    <div ><img :src="img1">
                    18K狗眼翡翠
                    </div>
                    <div class="add-info">品质:纯翡翠</div>
                    <div class="add-num normal-box">
                        <div class="left">购买数量</div><div class="right btn-component"> <button type="button">-</button><span>{{goodsNum}}</span><button type="button">+</button></div>
                    </div>
                    <div class="add-btn"><button type="button" @click="addCart">确定</button></div>
                </div>
        </mt-popup>
    </div>
</template>
<script>
import custHeader from  'components/custHeader'

	import {
	Swipe,
	SwipeItem,
    Popup
} from 'mint-ui';
import cookieUtil from 'js-cookie'
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.component(Popup.name, Popup);

export default {


  data() {
    return {
        pVisible:false,
    	title:'商品详情',
        goodsName:'',
        goodsCode:'',
        goodsNum:1,
    }

  },
   components:{
    custHeader
  },
  computed:{
      img1:function(){
          return require('assets/logo.png');
      }
  },
  created: function() {
      if (this.$route.query.goodsName) {
        this.goodsName = this.$route.query.goodsName
      }
      if (this.$route.query.goodsCodegoodsCode) {
        this.goodsCode = this.$route.query.goodsCode
      }
  },
  filters:{

  },
  methods: {
      addCartShow:function(){

          this.pVisible=true;
      },
      goBack: function() {
        this.$router.go(-1)
    },
    addCart:function(){
        var pars={
            goodsCode:this.goodsCode,
            goodsName:this.goodsName,
            spec:"规格描述",
            qty:1,
        }
        axiosUtil.upShopCartIns(pars).then(data=>{
            console.log("购物车新增");
            console.log(data);
        })
    }

  }
};
</script>
<style scoped>
._full_router{
	overflow: auto;
}
.text-contain{
	background: #eee;

}
.text-contain>div{
	padding: 0 12px ;
}

.pic-contain{
	width: 100vw;
	height: 80vw;
}
.pic-contain .mint-swipe{
	width: 100vw;
	height: 80vw;
}
.price-o{
	color: #aaa;
}
.price-o span{
	font-size: 12px;
	text-decoration: line-through;
}
.price-sales{
	font-size: 20px;
	color: #f40;
	}
	.price-sales span{
		font-size: 12px;
		border: 1px solid #f40;
		border-radius: 2px;
		padding: 2px;
	}
	.goods-name{
		margin: 6px 0;
		font-size: 16px;
		font-weight: bold;
	}
	.text-contain .goods-base-info{
		padding: 12px;
		background: #fff;
	}
	.text-contain .normal-box{
		margin-top: 12px;
		margin-bottom: 6px;
		text-align: left;
		font-size: 12px;
		color: #ccc;
	}
	.text-contain .no-pad{
		padding: 0;
	}
	.no-pad img{
		width: 100%;

	}
	.big-title{
		height: 30px;
		line-height: 30px;
		font-size: 12px;
		color: #999;
		text-align: center;
	}
	.fix-buy{
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		overflow: hidden;
		border-top: 1px solid #999;
	}
	.fix-buy button{
		width: 50%;
		float: left;
        height: 38px;
        border:none;
        color: #fff;
	}
    .add-cart{
        background: rgb(255, 129, 0);
    }
    .add-buy{
        background: #f40;
    }
    .add-cart-con{
        width: 100%;
    }
    .add-cart-contain{
        width: 100%;
        height: 50vh;
        position: relative;
    }
    .add-cart-contain img{
        width: 80px;
        vertical-align: middle;
    }
    .add-cart-contain .add-info{
        padding: 12px;
    }
    .add-cart-contain .add-num{
        padding: 12px;
    }
    .add-num .left{
        text-align: left;
    }
    .add-num .right{
        text-align: right;
    }
    .add-cart-contain .add-btn{
        position: absolute;
        bottom: 0;
        width: 100%;
    }
    .btn-component button{
        border: none;
        background: #eee;
        border-radius: 2px;
        padding: 0px 12px;
    font-size: 28px;
    color: #999;
    outline: none;
    }
    .btn-component span{
        padding: 0 12px;
    }
    .add-cart-contain .add-btn button{
        width: 100%;
        height: 38px;
        background: #f40;
        border: none;
        color: #fff;
    }
    .login-header {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      z-index: 3;
      height: 45px;
      line-height: 45px;
      font-size: 19px;
      background: rgba(0,0,0,0);
      color: #ffffff;
      text-align: center;
    }

    .top-back {
      max-width: 85px;
      float: left;
      width: 38px;
height: 38px;
background: rgba(0,0,0,.5);
border-radius: 38px;
line-height: 38px;
margin-left: 12px;
    }
    .cart-link{
        width: 38px;
height: 38px;
 background: rgba(0,0,0,.5);
line-height: 38px;
border-radius: 38px;
margin-right: 12px;

    }
    .cart-link a{
        color: #fff;
    }
</style>



